<template>
    <el-form ref="serviceformRef" :model="serviceform" label-width="100px">
        <el-form-item label="">
            <el-col>
                <el-row :gutter="1">
                    <el-col :span="14">
                        <el-input v-model="desc"></el-input>
                    </el-col>
                    <el-col :span="5">
                        <el-button type="primary" plain @click="addDesc" size="small"></el-button>
                    </el-col>
                    <el-col :span="5">
                        <el-button type="danger" plain @click="removeDesc" size="small"></el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-form-item>
        <el-form-item>
            <el-col justfy="start">
                <div v-for="(item,index) in serviceform.desc.split('-').slice(0,-1)" :key="item" class="text item">{{
                    index+1 +" . "+ item }}
                </div>
            </el-col>
        </el-form-item>
        <el-form-item label="">
            <el-col>
                <el-row :gutter="1">
                    <el-col :span="14">
                        <el-input v-model="record"></el-input>
                    </el-col>
                    <el-col :span="5">
                        <el-button type="primary" plain @click="addAction" size="small"></el-button>
                    </el-col>
                    <el-col :span="5">
                        <el-button type="danger" plain @click="removeAction" size="small"></el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-form-item>
        <el-form-item>
            <el-col justfy="start">
                <div v-for="(item,index) in serviceform.record.split('-').slice(0,-1)" :key="item" class="text item">{{
                    index+1 +" . "+ item }}
                </div>
            </el-col>
        </el-form-item>
        <el-form-item label="">
            <el-col>
                <upload @imagefile="getImage" :uploadHide="showupload"></upload>
            </el-col>
        </el-form-item>
        <el-form-item label="">
            <div>
                <el-select v-model="serviceform.style"
                           style="width: 100%"
                           placeholder="">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                    >
                    </el-option>
                </el-select>
            </div>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="serviceform.remark"
                      :rows="10"
                      type="textarea"></el-input>
        </el-form-item>
        <el-form-item>
            <div align="left">
                <el-select v-model="this.serviceform.orderid_id"
                           style="width: 100%"
                           placeholder=""
                           filterable>
                    <el-option
                            v-for="item in orderOptions"
                            :key="item.customer"
                            :label="item.customer"
                            :value="item.id"
                    >
                    </el-option>
                </el-select>
            </div>
        </el-form-item>
        <el-form-item>
            <el-col>
                <el-button type="primary" round @click="onSubmit"></el-button>
                <el-button type="warning" round></el-button>
            </el-col>
        </el-form-item>
    </el-form>
</template>

<script>
    import {BIconPlus} from 'bootstrap-icons-vue'
    import {Plus} from '@element-plus/icons-vue'
    import upload from './upload'
    import authorization from "../../utils/authorization";
    import axios from "axios";
    import products from "./product";
    //
    import {inject} from 'vue'

    export default ({
        name: "report",
        components: {
            BIconPlus,
            Plus,
            upload
        },
        setup() {
            const reload = inject('reload')
            const refreshPage = () => {
                console.log('from setup refreshPage, relaoding')
                reload && reload()
            }
            return {
                refreshPage
            }
        },
        data() {
            return {
                products: products.orders,
                options: [
                    {
                        value: 'servicein',
                        label: '',
                    },
                    {
                        value: 'serviceout',
                        label: '',
                    },
                    {
                        value: 'production',
                        label: '',
                    },
                ],
                serviceform: {
                    desc: "",
                    record: "",
                    pictures: "",
                    orderid_id: '',
                    style: "servicein",
                    remark: "",
                },
                desc: "",
                record: "",
                showupload: true,
                //
                orderOptions: [],
            }
        },
        methods: {
            addDesc() {
                this.serviceform.desc += this.desc + '-'
            },
            addAction() {
                this.serviceform.record += this.record + '-'
            },
            removeAction() {
                let temp = this.serviceform.record.split('-').slice(0, -1)
                this.serviceform.record = temp.join('-')
            },
            removeDesc() {
                let temp = this.serviceform.desc.split('-').slice(0, -1)
                this.serviceform.desc = temp.join('-')
            },
            getImage(data) {
                this.serviceform.pictures = data.raw
                this.showupload = data === "";
            },
            onSubmit() {
                let param = new FormData()
                param.append('pictures', this.serviceform.pictures)
                param.append('orderid_id', this.serviceform.orderid_id)
                param.append('description', this.serviceform.desc)
                param.append('style', this.serviceform.style)
                param.append('actions', this.serviceform.record)
                param.append('remark', this.serviceform.remark)
                //
                let auth = true;
                authorization().then(function (response) {
                    // 
                    if (!response[0]) {
                        alert('');
                        auth = false;
                    }
                });
                // 
                if (auth) {
                    axios.post('/api/home/service/', param,
                        {
                            headers: {
                                Authorization: 'Bearer ' + localStorage.getItem('access.product'),
                            }
                        })
                        .then(res => {
                            this.refreshPage()
                            console.log(res)
                        })
                }
            },
            // ,
            async getProduct() {
                let a = {};
                await axios.get('/api/home/product/').then(res => {
                    a = res.data.results;
                })
                return a;
            },
        },
        // 
        async created() {
            // 
            this.orderOptions = this.products.allorder;
            // console.log('orderOptions:', this.orderOptions)
        }
    })

</script>

<style>
    .el-form-item {
        margin-top: 5px;
        margin-bottom: 5px;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
    }

    .el-form-item__content {
        margin-right: 5px;
    }
</style>

